jQuery(document).ready(function () {
    var selectedProductType = jQuery('#product-type').val();
    var $group_pricing = '#general_product_data, .options_group.pricing';
    var rememberVirtualChecked = {
        [selectedProductType]: jQuery('#_virtual').prop('checked')
    };
	//jQuery($group_pricing).addClass('show_if_courses').addClass('show_if_license');
	jQuery('.options_group.pricing').addClass('show_if_license').addClass('show_if_courses');
	jQuery('.form-field._tax_status_field').parent().addClass('show_if_courses').addClass('show_if_license')

	//Show general tab + pricing fields when page load
	//and license or courses product type is selected
	if ('license' === selectedProductType || 'courses' === selectedProductType) {
		jQuery('li.general_options').show(); //Show general tab
		jQuery('li.general_options a').trigger('click'); //Imitate general tab clicked
		jQuery('.options_group.pricing').removeClass('hidden').show(); //Show pricing fields
		jQuery('.form-field._tax_status_field').parent().show(); //Show tax fields
		jQuery('li.attribute_options').removeClass('hide_if_courses').addClass('show_if_courses').show();
		jQuery('li.linked_product_options').removeClass('hide_if_courses').addClass('show_if_courses').show();
	}

	if ('subscription' === selectedProductType || 'license' === selectedProductType) {
		jQuery('#ulgm_swap_license_product_wrapper').show();
	}

	if (jQuery().select2) {
		jQuery('#ulgm_swap_license_product').select2({
			theme: "classic"
		});

		jQuery('#_ulgm_course').select2({
			theme: "classic",
			width: "350px",
		});

		jQuery('#_ulgm_license').select2({
			theme: "classic",
			width: "350px",
		});
	}

    //When virtual checkbox is changed store Type and Checked Prop.
    jQuery('#_virtual').change(function () {
        rememberVirtualChecked[selectedProductType] = jQuery(this).prop('checked');
    })

	//When product type is changed
	jQuery('#product-type').change(function () {

		var newProductType = jQuery(this).val();

        // Bail if no change. - initial load issue
        if (selectedProductType === newProductType) {
            return;
        }
        selectedProductType = newProductType;

        var ignore = ['group', 'subscription', 'license', 'courses'];
        if (!ignore.includes(selectedProductType) && rememberVirtualChecked.hasOwnProperty(selectedProductType)) {
            // Restore the previous state of the checkbox for this product type.
            jQuery('#_virtual').prop('checked', rememberVirtualChecked[selectedProductType]);
        } else {
            jQuery('#_virtual').prop('checked', false);
        }

		jQuery('#ulgm_swap_license_product_wrapper').hide();

		if ('courses' === selectedProductType) {
			jQuery('#_virtual').prop('checked', true);
			jQuery('li.general_options').show()
			jQuery('li.attribute_options').removeClass('hide_if_courses').addClass('show_if_courses')
			jQuery('#advanced_product_data').hide()
			jQuery('.advanced_options').removeClass('active')
			jQuery($group_pricing).removeClass('active').show()
		}

		if ('group' === selectedProductType) {
			jQuery('li.general_options').show().addClass('active')
			jQuery('#advanced_product_data').hide()
			jQuery('.advanced_options').removeClass('active')
			jQuery($group_pricing).removeClass('active').show()
		}

		//selectedProductType = jQuery('#product-type').val();
		if ('license' === selectedProductType) {
			jQuery('#_virtual').prop('checked', true);
			jQuery('li.general_options').show()
			jQuery('#advanced_product_data').hide()
			jQuery('.advanced_options').removeClass('active')
			jQuery('#general_product_data').hide()
			jQuery($group_pricing).removeClass('hidden').show()
			jQuery('#ulgm_swap_license_product_wrapper').show();
		}

		if ('subscription' === selectedProductType) {
			jQuery('#ulgm_swap_license_product_wrapper').show();
		}

	})
})
